/* src/index.css - Plain CSS for portfolio */
:root{
  --bg:#0f1724;
  --card:#0b1220;
  --accent:#2563eb;
  --muted:#94a3b8;
  --light:#ffffff;
}

*{box-sizing:border-box;margin:0;padding:0;font-family:Inter,system-ui,Arial,Helvetica,sans-serif}

body{
  background: linear-gradient(180deg,#071025 0%, #081226 100%);
  color:var(--light);
  min-height:100vh;
  -webkit-font-smoothing:antialiased;
}

/* Navbar */
.navbar{
  width:100%;
  position:fixed;
  top:0;
  left:0;
  z-index:40;
  background:rgba(6,10,18,0.7);
  backdrop-filter:blur(6px);
  padding:14px 24px;
  display:flex;
  align-items:center;
  justify-content:space-between;
}
.navbar .brand{font-weight:700;font-size:20px}
.navbar .links{display:flex;gap:18px}
.navbar a{color:var(--muted);text-decoration:none}
.navbar a:hover{color:var(--light)}

/* Hero */
.hero{
  display:flex;
  align-items:center;
  justify-content:center;
  min-height:90vh;
  padding-top:70px;
  text-align:center;
}
.hero .inner{max-width:900px;padding:30px}
.hero h1{font-size:48px;margin-bottom:12px}
.hero p{color:var(--muted);font-size:18px;margin-bottom:18px}
.cta{
  background:var(--accent);
  color:#fff;
  padding:12px 20px;
  border-radius:10px;
  text-decoration:none;
  display:inline-block;
  font-weight:600;
}

/* Sections */
.section{max-width:1100px;margin:60px auto;padding:20px}
.section h2{font-size:30px;margin-bottom:18px}
.card-grid{display:grid;grid-template-columns:repeat(auto-fit,minmax(260px,1fr));gap:18px}
.card{background:linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01)); padding:18px;border-radius:12px;box-shadow:0 6px 18px rgba(2,6,23,0.6)}
.card h3{margin-bottom:8px}
.card p{color:var(--muted);font-size:14px;margin-bottom:12px}

/* Contact form */
.contact-form{max-width:720px;margin:0 auto}
.input, .textarea{
  width:100%;
  padding:10px 12px;
  margin-bottom:12px;
  border-radius:8px;
  background:#071126;
  color:var(--light);
  border:1px solid rgba(255,255,255,0.04);
}
.button{
  background:var(--accent);
  color:#fff;
  padding:10px 16px;
  border-radius:8px;
  cursor:pointer;
  border:none;
  font-weight:600;
}

/* Footer */
.footer{padding:30px;text-align:center;color:var(--muted);margin-top:40px}
@media (max-width:640px){
  .hero h1{font-size:32px}
  .hero p{font-size:16px}
}
