/* Project.css - simple modern styles (no Tailwind) */

:root{
  --bg:#0b1220;
  --card:#0f1724;
  --muted:#94a3b8;
  --accent:#2563eb;
  --accent-dark:#1d4ed8;
  --light:#ffffff;
}

.projects-section{
  background: linear-gradient(180deg,#071025 0%, #081226 100%);
  padding: 60px 20px;
  color: var(--light);
}

.projects-container{
  max-width: 1100px;
  margin: 0 auto;
}

.projects-title{
  text-align: center;
  font-size: 32px;
  margin-bottom: 28px;
  color: var(--light);
  font-weight: 700;
}

/* Grid */
.projects-grid{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 22px;
}

/* Card */
.project-card{
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(2,6,23,0.6);
  display: flex;
  flex-direction: column;
  transition: transform .22s ease, box-shadow .22s ease;
}

.project-card:hover{
  transform: translateY(-6px);
  box-shadow: 0 18px 36px rgba(2,6,23,0.75);
}

.project-image-wrap{
  width: 100%;
  height: 180px;
  overflow: hidden;
  background: linear-gradient(90deg, rgba(255,255,255,0.01), rgba(255,255,255,0));
}

.project-image{
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

/* Body */
.project-body{
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  flex: 1 1 auto;
}

.project-name{
  font-size: 20px;
  margin: 0;
  color: var(--light);
}

.project-desc{
  color: var(--muted);
  font-size: 14px;
  margin: 0;
  line-height: 1.5;
}

/* tech badges */
.project-tech{
  display:flex;
  gap:8px;
  flex-wrap:wrap;
}

.tech-badge{
  background: rgba(96,165,250,0.12);
  color: #60a5fa;
  font-size: 12px;
  padding: 6px 10px;
  border-radius: 999px;
  border: 1px solid rgba(96,165,250,0.08);
}

/* buttons */
.project-links{
  margin-top: auto;
  display:flex;
  gap:10px;
  align-items:center;
}

.btn{
  padding:10px 14px;
  border-radius:8px;
  text-decoration:none;
  font-weight:600;
  font-size:14px;
  display:inline-block;
  text-align:center;
}

.btn-outline{
  background: transparent;
  border: 1px solid rgba(255,255,255,0.06);
  color: var(--light);
}

.btn-primary{
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent-dark);
}

/* footer link */
.projects-footer{
  text-align:center;
  margin-top: 18px;
}

.view-all{
  color: var(--muted);
  text-decoration:none;
  font-weight:600;
}

.view-all:hover{ color: var(--light); }

/* Responsive */
@media (max-width: 980px){
  .projects-grid{ grid-template-columns: repeat(2, 1fr); }
  .project-image-wrap{ height:160px; }
}
@media (max-width: 640px){
  .projects-grid{ grid-template-columns: 1fr; }
  .project-image-wrap{ height:200px; }
  .projects-title{ font-size: 26px; }
}
